.csb {
  overflow-y: scroll;
  height: 150px;
  width: 300px;
  margin: 10px 5px;
  padding: 5px;
  text-align: justify;
}
.csb::-webkit-scrollbar {
  width: 5px;
  border-radius: 5px;
  background-color: #ffffff66;
}

.csb::-webkit-scrollbar-thumb {
  width: 9px;
  border-radius: 5px;
  background: linear-gradient(180deg, #7100dc 0%, #ff6a57 100%);
}
